.banner{
  &__rcy-640-100{
    position: relative;
    width: 640px;
    height: 100px;
  }
  &__wrap-rcy-640-100{
    padding: 5px 4px 2px 5px;
  }
  &__body-rcy-640-100{
    padding-top: 13px;
    padding-left: 20px;
    border: 1px solid #000;
  }
  &__rcy-640-100-wr-desc{
    max-width: 210px;
  }
  &__rcy-640-100-title{
    font-size: 22px;
    line-height: 22px;
    letter-spacing: -0.015em;
  }
  &__rcy-640-100-paragraph{
    margin-top: 8px;
    font-size: 14px;
    line-height: 16px;
    letter-spacing: -0.015em;
  }

  &__rcy-640-100-img{
    right: 163px;
    max-width: 178px;
  }
  &__rcy-640-100-logo{
    max-width: 104px;
    top: 38px;
    right: 20px;
  }
  &__rcy-640-100-circle{
    top: 4px;
    right: 249px;
    width: 93px;
    height: 93px;
    border-radius: 50%;
  }
}
