.banner{
  &__rcy-300{
    position: relative;
    width: 300px;
    height: 250px;
  }
  &__wrap-rcy-300{
    padding: 8px;
  }
  &__body-rcy-300{
    padding-top: 50px;
    padding-left: 12px;
    border: 1px solid #000;
  }
  &__rcy-300-wr-desc{
    max-width: 176px;
  }
  &__rcy-300-title{
    font-size: 20px;
    line-height: 22px;
    letter-spacing: -0.015em;
  }
  &__rcy-300-paragraph{
    margin-top: 14px;
    font-size: 16px;
    line-height: 18px;
    letter-spacing: -0.015em;
  }

  &__rcy-300-img{
    right: 0;
    max-width: 217px;
  }
  &__rcy-300-logo{
    max-width: 80px;
    top: 34px;
    right: 16px;
  }
  &__rcy-300-circle{
    top: 97px;
    right: 15px;
    width: 140px;
    height: 140px;
    border-radius: 50%;
  }
}
