
.banner{
  &__rambler{
    position: relative;
    width: 100%;
    max-width: 1920px;
    min-height: 600px;
  }
  &__wrap-rambler{
    position: relative;

  }
  &__body-rambler{
    display: flex;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    padding-top: 145px;
    padding-left: 40px;
  }
  &__rambler-wr-desc{
    width: 100%;
    max-width: 790px;
    min-width: 790px;
  }
  &__rambler-title{
    font-size: 90px;
    line-height: 90px;
    letter-spacing: -0.015em;
  }
  &__rambler-paragraph{
    margin-top:40px;
    font-size: 60px;
    line-height: 62px;
    letter-spacing: -0.015em;
  }
  &__rambler-wr-img{
    position: relative;
    display: flex;
    margin-top: auto;
    .banner__img{
      position: relative;
    }
  }
  &__rambler-img{
    right: 6%;
    transform: translateY(-50%);
    width: 100%;
    max-width: 981px;
    &:before{
      content: "";
      display: block;
      padding-top: 48.8%;
    }

  }
  &__rambler-logo{
    max-width: 287px;
    top: 83px;
    right: 4%;
  }
  &__rambler-circle{
    bottom: 0;
    right: 27%;
    border-radius: 50%;
    width: 30vw;
    max-width: 560px;
    min-width: 300px;
    &:before{
      content: "";
      display: block;
      padding-top: 100%;
    }
  }
}

@media screen and (max-width: 1280px){
  .banner__rambler-wr-img {
    width: 32vw;
  }
  .banner__rambler-img {
    position: absolute;
    right: -18px;
    width: 45vw;
  }
  .banner__rambler-wr-img .banner__img {
    position: absolute;
  }
}
@media screen and (max-width: 930px){
  .banner__rambler-logo {
    max-width: 147px;
    top: 60px;
    right: 0;
    left: 50%;
    transform: translateX(-50%);
  }
  .banner__rambler-wr-desc{
    min-width: 0;
    max-width: 590px;
  }
  .banner__body-rambler{
    padding-left: 10px;
    flex-direction: column;
  }
  .banner__rambler-title {
    font-size: 51px;
    line-height: 65px;
    letter-spacing: -.015em;
  }
  .banner__rambler-img {
    position: absolute;
    width: 100%;
    max-width: 431px;
    right: auto;
    left: 50%;
    transform: translateX(-50%);
  }
  .banner__rambler-paragraph {
    margin-top: 40px;
    font-size: 32px;
    line-height: 44px;
    letter-spacing: -.015em;
  }
  .banner__rambler-wr-img {
    width: 100%;
  }

  .banner__rambler-circle{
    right: auto;
    left: 50%;
    transform: translateX(-50%);
  }
}

