.banner{
  &__rambler-300-600{
    position: relative;
    width: 300px;
    height: 600px;
  }
  &__body-rambler-300-600{
    padding-top: 161px;
    padding-left: 20px;
  }
  &__rambler-300-600-wr-desc{
    max-width: 264px;
  }
  &__rambler-300-600-title{
    font-size: 30px;
    line-height: 30px;
    letter-spacing: -0.015em;
  }
  &__rambler-300-600-paragraph{
    margin-top:16px;
    font-size: 24px;
    line-height: 24px;
    letter-spacing: -0.015em;
  }

  &__rambler-300-600-img{
    right: 0;
    max-width: 300px;
  }
  &__rambler-300-600-logo{
    max-width: 245px;
    top: 60px;
    left: 50%;
    transform: translateX(-50%);
  }
  &__rambler-300-600-circle{
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 270px;
    height: 270px;
    border-radius: 50%;
  }
}
