.banner{
  &__rambler-1920-600{
    position: relative;
    width: 1920px;
    height: 600px;
  }
  &__body-rambler-1920-600{
    padding-top: 144px;
    padding-left: 39px;
  }
  &__rambler-1920-600-wr-desc{
    max-width: 790px;
  }
  &__rambler-1920-600-title{
    font-size: 90px;
    line-height: 90px;
    letter-spacing: -0.015em;
  }
  &__rambler-1920-600-paragraph{
    margin-top:40px;
    font-size: 60px;
    line-height: 62px;
    letter-spacing: -0.015em;
  }

  &__rambler-1920-600-img{
    right: 13%;
    max-width: 981px;
  }
  &__rambler-1920-600-logo{
    max-width: 374px;
    top: 70px;
    right: 40px;
  }
  &__rambler-1920-600-circle{
    bottom: 0;
    right: 28%;
    width: 560px;
    height: 560px;
    border-radius: 50%;
  }
}
