.banner{
  &__mail-300-300{
    position: relative;
    width: 300px;
    height: 300px;
  }
  &__body-mail-300-300{
    padding-top: 72px;
    padding-left: 20px;
  }
  &__mail-300-300-wr-desc{
    max-width: 230px;
  }
  &__mail-300-300-title{
    font-size: 26px;
    line-height: 26px;
    letter-spacing: -0.015em;
  }
  &__mail-300-300-paragraph{
    margin-top:16px;
    font-size: 18px;
    line-height: 20px;
    letter-spacing: -0.015em;
  }

  &__mail-300-300-img{
    right: 0;
    max-width: 210px;
  }
  &__mail-300-300-logo{
    max-width: 80px;
    top: 40px;
    right: 16px;
  }
  &__mail-300-300-circle{
    bottom: 0;
    right: 20px;
    width: 140px;
    height: 140px;
    border-radius: 50%;
  }
}
