.banner{
  &__rcy-300-500{
    position: relative;
    width: 300px;
    height: 500px;
  }
  &__wrap-rcy-300-500{
    padding: 8px;
  }
  &__body-rcy-300-500{
    padding-top: 109px;
    padding-left: 10px;
    border: 1px solid #000;
  }
  &__rcy-300-500-wr-desc{
    max-width: 246px;
  }
  &__rcy-300-500-title{
    font-size: 28px;
    line-height: 26px;
    letter-spacing: -0.015em;
  }
  &__rcy-300-500-paragraph{
    margin-top: 16px;
    font-size: 24px;
    line-height: 24px;
    letter-spacing: -0.015em;
  }

  &__rcy-300-500-img{
    right: 0;
    max-width: 268px;
  }
  &__rcy-300-500-logo{
    max-width: 147px;
    top: 39px;
    left: 50%;
    transform: translateX(-50%);
  }
  &__rcy-300-500-circle{
    top: 249px;
    right: 9px;
    width: 240px;
    height: 240px;
    border-radius: 50%;
  }
}
