.banner{
  &__wrap{
    position: relative;
    height: 100%;
  }
  &__rcy{

  }
  height: 100%;
  &__body{
    height: 100%;
    opacity: 0;
    animation-duration: 3s;
    animation-timing-function: ease-in-out;
    animation-name: bunner-animate;
    animation-fill-mode:forwards;
    animation-delay: 1s;
  }
  &__wr-description{
    width: 100%;
  }
  &__title{
    position: relative;
    font-family: $ff-bold;
    color: $color-text;
  }
  &__paragraph{
    font-family: $ff-bold;
    color: $color-text;
  }
  &__btn{
    cursor: pointer;
    color: #FFFFFF;
    width: 100%;
    border: none;
    font-family: $ff-medium;
    transform: perspective(1px) translateZ(0);
    position: relative;
    overflow: hidden;
    background: #fff;
    transition-property: color;
    transition-duration: 0.3s;
    &:link,
    &:active {
      outline: none;
      text-decoration: none;
      outline-width: 0;
      color: #fff;
      background: #fff;
    }
    &:before{
      content: "";
      position: absolute;
      z-index: -1;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: #8BC540;
      border-radius: 100%;
      transform: scale(2);
      transition-property: transform;
      transition-duration: 0.3s;
      transition-timing-function: ease-out;
    }
    &:hover{
      color: #8BC540;
      border: 1px solid #8BC540;
    }
    &:hover:before,&:hover:focus,&:hover:active{
      transform: scale(0);
    }

  }
  &__img{
    position: absolute;
    bottom: 0;
    width: 100%;
    z-index: -2;
    transform: scale(0);
    animation-duration: 2s;
    animation-delay: 2s;
    animation-timing-function: ease-in-out;
    animation-name: img-animate;
    transform-origin: center;
    animation-fill-mode:forwards;
    display: flex;
    align-items: flex-end;
  }
  &__logo{
    position: absolute;
    width: 100%;
  }
  &__circle{
    position: absolute;
    z-index: -3;
    border-radius: 50%;
    background: linear-gradient(131.25deg, rgba(139, 197, 64, 0) 23.57%, rgba(139, 197, 64, 0.1) 44.65%, #ABE066 82.28%);
  }
  &__title-span{
    position: relative;
    display: inline-block;
    margin-left: 20px;
    svg {
      left: 50%;
      position: absolute;
      top: 50%;
      transform: translate(-50%, -50%);
      path {
        animation-duration: 2s;
        animation-delay: 2s;
        animation-iteration-count:infinite;
        animation-timing-function: ease-in-out;
        animation-name: svg-line-animate;
        animation-direction: alternate;
      }
    }
    &.active {
      svg {
        path {
          stroke-dashoffset: 0;
        }
      }
    }
  }

}
@keyframes bunner-animate {
  0% {
    opacity: 0;

  }
  100% {
    opacity: 1;
  }
}
@keyframes img-animate {
  0% {
    transform: scale(0);

  }
  100% {
    transform: scale(1);
  }
}
@keyframes svg-line-animate {

  100% {
    stroke-dashoffset: 0;
  }
}
