.banner{
  &__rcy-640-960{
    position: relative;
    width: 640px;
    height: 960px;
  }
  &__wrap-rcy-640-960{
    padding: 8px;
  }
  &__body-rcy-640-960{
    padding-top: 230px;
    padding-left: 30px;
    border: 1px solid #000;
  }
  &__rcy-640-960-wr-desc{
    max-width: 580px;
  }
  &__rcy-640-960-title{
    font-size: 66px;
    line-height: 66px;
    letter-spacing: -0.015em;
  }
  &__rcy-640-960-paragraph{
    margin-top: 32px;
    font-size: 50px;
    line-height: 50px;
    letter-spacing: -0.015em;
  }

  &__rcy-640-960-img{
    right: 0;
    max-width: 621px;
  }
  &__rcy-640-960-logo{
    max-width: 300px;
    top: 80px;
    left: 50%;
    transform: translateX(-50%);
  }
  &__rcy-640-960-circle{
    bottom: 10px;
    right: 47px;
    width: 440px;
    height: 440px;
    border-radius: 50%;
  }
}
