.banner{
  &__mail-1920-250{
    position: relative;
    width: 1920px;
    height: 250px;
  }
  &__body-mail-1920-250{
    padding-top: 69px;
    padding-left: 40px;
  }
  &__mail-1920-250-wr-desc{
    max-width: 925px;
  }
  &__mail-1920-250-title{
    font-size: 62px;
    line-height: 62px;
    letter-spacing: -0.015em;
  }
  &__mail-1920-250-paragraph{
    margin-top:16px;
    font-size: 34px;
    line-height: 34px;
    letter-spacing: -0.015em;
  }

  &__mail-1920-250-img{
    right: 23%;
    max-width: 409px;
  }
  &__mail-1920-250-logo{
    max-width: 287px;
    top: 83px;
    right: 4%;
  }
  &__mail-1920-250-circle{
    bottom: 0;
    right: 32%;
    width: 240px;
    height: 240px;
    border-radius: 50%;
  }
}
