.banner{
  &__rambler-1280-600{
    position: relative;
    width: 1280px;
    height: 600px;
  }
  &__body-rambler-1280-600{
    padding-top: 101px;
    padding-left: 40px;
  }
  &__rambler-1280-600-wr-desc{
    max-width: 790px;
  }
  &__rambler-1280-600-title{
    font-size: 90px;
    line-height: 90px;
    letter-spacing: -0.015em;
  }
  &__rambler-1280-600-paragraph{
    margin-top:40px;
    font-size: 60px;
    line-height: 62px;
    letter-spacing: -0.015em;
  }

  &__rambler-1280-600-img{
    right: 0;
    max-width: 773px;
  }
  &__rambler-1280-600-logo{
    max-width: 245px;
    top: 56px;
    right: 20px;
  }
  &__rambler-1280-600-circle{
    bottom: 0;
    right: 5%;
    width: 450px;
    height: 450px;
    border-radius: 50%;
  }
}
