.banner{
  &__rcy-300-600{
    position: relative;
    width: 300px;
    height: 600px;
  }
  &__wrap-rcy-300-600{
    padding: 8px;
  }
  &__body-rcy-300-600{
    padding-top: 153px;
    padding-left: 10px;
    border: 1px solid #000;
  }
  &__rcy-300-600-wr-desc{
    max-width: 264px;
  }
  &__rcy-300-600-title{
    font-size: 30px;
    line-height: 30px;
    letter-spacing: -0.015em;
  }
  &__rcy-300-600-paragraph{
    margin-top: 16px;
    font-size: 24px;
    line-height: 24px;
    letter-spacing: -0.015em;
  }

  &__rcy-300-600-img{
    right: 0;
    max-width: 300px;
  }
  &__rcy-300-600-logo{
    max-width: 147px;
    top: 60px;
    left: 50%;
    transform: translateX(-50%);
  }
  &__rcy-300-600-circle{
    top: 312px;
    right: 8px;
    width: 270px;
    height: 270px;
    border-radius: 50%;
  }
}
