.banner{
  &__rcy-960{
    position: relative;
    width: 960px;
    height: 640px;
  }
  &__wrap-rcy-960{
    padding: 8px;
  }
  &__body-rcy-960{
    padding-top: 110px;
    padding-left: 30px;
    border: 1px solid #000;
  }
  &__rcy-960-wr-desc{
    max-width: 615px;
  }
  &__rcy-960-title{
    font-size: 70px;
    line-height: 80px;
    letter-spacing: -0.015em;
  }
  &__rcy-960-paragraph{
    margin-top: 26px;
    font-size: 50px;
    line-height: 52px;
    letter-spacing: -0.015em;
  }

  &__rcy-960-img{
    right: 0;
    max-width: 688px;
  }
  &__rcy-960-logo{
    max-width: 220px;
    top: 63px;
    right: 43px;
  }
  &__rcy-960-circle{
    top: 175px;
    right: 55px;
    width: 440px;
    height: 440px;
    border-radius: 50%;
  }
}
