.banner{
  &__rcy-240{
    position: relative;
    width: 240px;
    height: 400px;
  }
  &__wrap-rcy-240{
    padding: 8px;
  }
  &__body-rcy-240{
    padding-top: 82px;
    padding-left: 10px;
    border: 1px solid #000;
  }
  &__rcy-240-wr-desc{
    max-width: 210px;
  }
  &__rcy-240-title{
    font-size: 24px;
    line-height: 26px;
    letter-spacing: -0.015em;
  }
  &__rcy-240-paragraph{
    margin-top: 16px;
    font-size: 20px;
    line-height: 22px;
    letter-spacing: -0.015em;
  }

  &__rcy-240-img{
    right: 0;
    max-width: 240px;
  }
  &__rcy-240-logo{
    max-width: 80px;
    top: 36px;
    right: 20px;
  }
  &__rcy-240-circle{
    top: 230px;
    right: 21px;
    width: 160px;
    height: 160px;
    border-radius: 50%;
  }
}
