.banner{
  &__rcy-640-200{
    position: relative;
    width: 640px;
    height: 200px;
  }
  &__wrap-rcy-640-200{
    padding: 8px;
  }
  &__body-rcy-640-200{
    padding-top: 32px;
    padding-left: 20px;
    border: 1px solid #000;
  }
  &__rcy-640-200-wr-desc{
    max-width: 316px;
  }
  &__rcy-640-200-title{
    font-size: 36px;
    line-height: 36px;
    letter-spacing: -0.015em;
  }
  &__rcy-640-200-paragraph{
    margin-top: 24px;
    font-size: 20px;
    line-height: 20px;
    letter-spacing: -0.015em;
  }

  &__rcy-640-200-img{
    right: 49px;
    max-width: 270px;
  }
  &__rcy-640-200-logo{
    max-width: 104px;
    top: 32px;
    right: 20px;
  }
  &__rcy-640-200-circle{
    top: 10px;
    right: 127px;
    width: 180px;
    height: 180px;
    border-radius: 50%;
  }
}
