.banner{
  &__rcy-336{
    position: relative;
    width: 336px;
    height: 280px;
  }
  &__wrap-rcy-336{
    padding: 8px;
  }
  &__body-rcy-336{
    padding-top: 54px;
    padding-left: 10px;
    border: 1px solid #000;
  }
  &__rcy-336-wr-desc{
    max-width: 193px;
  }
  &__rcy-336-title{
    font-size: 22px;
    line-height: 22px;
    letter-spacing: -0.015em;
  }
  &__rcy-336-paragraph{
    margin-top: 16px;
    font-size: 18px;
    line-height: 20px;
    letter-spacing: -0.015em;
  }

  &__rcy-336-img{
    right: 0;
    max-width: 261px;
  }
  &__rcy-336-logo{
    max-width: 94px;
    top: 30px;
    right: 20px;
  }
  &__rcy-336-circle{
    top: 110px;
    right: 21px;
    width: 160px;
    height: 160px;
    border-radius: 50%;
  }
}
