.banner{
  &__yandex-desctop{
    position: relative;
    width: 1200px;
    height: 675px;
  }
  &__body-yan-des{
    padding-left: 40px;
    padding-top: 80px;
  }
  &__yan-des-wr-desc{
    max-width: 790px;
  }
  &__yan-des-title{
    font-size: 90px;
    line-height: 90px;
    letter-spacing: -0.015em;
  }
  &__yan-des-paragraph{
    margin-top: 38px;
    font-size: 60px;
    line-height: 62px;
    letter-spacing: -0.015em;
  }
  &__yan-des-btn{
    margin-top: 80px;
    height: 80px;
    max-width: 280px;
    font-size: 28px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
  }
  &__yan-des-img{
    right: 0;
    max-width: 773px;
  }
  &__yan-des-logo{
    max-width: 243px;
    top: 69px;
    right: 22px;
  }
  &__yan-des-circle{
    top: 205px;
    right: 55px;
    width: 460px;
    height: 460px;
    border-radius: 50%;
  }
}
