.banner{
  &__rcy-970-250{
    position: relative;
    width: 970px;
    height: 250px;
  }
  &__wrap-rcy-970-250{
    padding: 8px;
  }
  &__body-rcy-970-250{
    padding-top: 43px;
    padding-left: 30px;
    border: 1px solid #000;
  }
  &__rcy-970-250-wr-desc{
    max-width: 440px;
  }
  &__rcy-970-250-title{
    font-size: 46px;
    line-height: 46px;
    letter-spacing: -0.015em;
  }
  &__rcy-970-250-paragraph{
    margin-top: 24px;
    font-size: 30px;
    line-height: 30px;
    letter-spacing: -0.015em;
  }

  &__rcy-970-250-img{
    right: 110px;
    max-width: 359px;
  }
  &__rcy-970-250-logo{
    max-width: 168px;
    top: 48px;
    right: 20px;
  }
  &__rcy-970-250-circle{
    top: 20px;
    right: 241px;
    width: 220px;
    height: 220px;
    border-radius: 50%;
  }
}
