.banner{
  &__mail-240-400{
    position: relative;
    width: 240px;
    height: 400px;
  }
  &__body-mail-240-400{
    padding-top: 100px;
    padding-left: 20px;
  }
  &__mail-240-400-wr-desc{
    max-width: 211px;
  }
  &__mail-240-400-title{
    font-size: 24px;
    line-height: 26px;
    letter-spacing: -0.015em;
  }
  &__mail-240-400-paragraph{
    margin-top:16px;
    font-size: 20px;
    line-height: 22px;
    letter-spacing: -0.015em;
  }

  &__mail-240-400-img{
    right: 0;
    max-width: 240px;
  }
  &__mail-240-400-logo{
    max-width: 80px;
    top: 55px;
    right: 20px;
  }
  &__mail-240-400-circle{
    bottom: 0;
    right: 20px;
    width: 160px;
    height: 160px;
    border-radius: 50%;
  }
}
