.banner{
  &__yandex-mobile{
    position: relative;
    width: 640px;
    height: 360px;
  }
  &__body-yan-mob{
    padding-left: 20px;
    padding-top: 72px;
  }
  &__yan-mob-wr-desc{
    max-width: 351px;
  }
  &__yan-mob-title{
    font-size: 40px;
    line-height: 40px;
    letter-spacing: -0.015em;
  }
  &__yan-mob-paragraph{
    margin-top: 24px;
    font-size: 26px;
    line-height: 28px;
    letter-spacing: -0.015em;
  }
  &__yan-mob-btn{
    margin-top: 46px;
    height: 50px;
    max-width: 150px;
    font-size: 14px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
  }
  &__yan-mob-img{
    right: 0;
    max-width: 426px;
  }
  &__yan-mob-logo{
    max-width: 134px;
    top: 38px;
    right: 20px;
  }
  &__yan-mob-circle{
    bottom: 0;
    right: 37px;
    width: 260px;
    height: 260px;
    border-radius: 50%;
    background: linear-gradient(131.25deg, rgba(139, 197, 64, 0) 23.57%, rgba(139, 197, 64, 0.1) 44.65%, #ABE066 82.28%);
  }
}
